import * as React from "react"
import photoContent4 from './../../Image/Mask group.png'
import documentBackground2 from './../../Image/TO.png'
import backgroundVector from './../../Image/Vector 1.png'
import photoContent5 from './../../Image/Fly.png'
import style from './Education.module.css'
import {Col, Row, Typography} from 'antd';


const imageTable2 = [
    {
        key: 4,
        src: photoContent4,
        className: style.photoContent4
    },
    {
        key: 5,
        src: photoContent5,
        className: style.photoContent5
    },

];

const Education = () => {
    return (

        <>
            <img src={documentBackground2} className={style.documentBackground2}/>
            <img src={backgroundVector} className={style.backgroundVector}/>
            <Row id='002'>
                <Col  lg={{span: 12, order: 0}} xs={{span: 3, order: 1}}>
                    {imageTable2.map((el, index) => <img key={index} src={el.src} className={el.className}/>)}
                </Col>
                <Col   lg={{span: 12,}} xs={{span: 24}}>
                    <h3 className={style.titleContent}>Dowiadczenie</h3>
                    <Typography className={style.paragraf}>
                        <b>Wsppracowaem z wieloma podmiotami </b> takimi jak:
                        <ul className={style.educationList}>
                            <li>Muzeum Bitwy pod Grunwaldem (7 sezonw bada),</li>
                            <li>Muzeum Pocztkw Pastwa Polskiego,</li>
                            <li>Muzeum Obozw Jenieckich w aganiu,</li>
                            <li>Muzeum Pogranicza w Dziadowie,</li>
                            <li>Lubelski Konserwator Zabytkw,</li>
                            <li>Lubelski Konserwator Zabytkw,</li>
                            <li>prowadziem poszukiwania na zamku krlw i alchemikw w Krzepicach.</li>
                        </ul>
                    </Typography>

                    <Typography className={style.paragraf1}>
                        <b>Wsppracowaem z wieloma czoowymi producentami wykrywaczy metalu </b>
                        w zakresie testowania i promocji na moim polu testowym Mocny Sygna.
                    </Typography>
                </Col>
            </Row>


        </>

    )
};

export default Education;